// Colors
$dodger-blue: #1783FA;
$dodger-blue-01: #1AA0FC;
$blue-bayoux: #526474;
$hit-gray: #A9B1B9;
$rhino: #273D51;
$mountain-meadow: #0ECB81;
$brick-red: #CF304A;
$my-sin: #ffb82a;
$shark: #1E2329;
$catskill-white: #F6F9FB;
$downriver: #0A2540;
$blue-romance: #D1F4D9;
$humming-bird: #D0F3F8;
$peach: #FFE4BC;
$cosmos: #FED6DB;
$iron: #D4D8DC;
$indigo: #376BC8;
$azure: #2F5BAA;
$wild-sand: #F6F6F6;
$alice-blue: #F3FAFF;
$denim: #1A62C4;
$gossamer: #0A9E64;
$emerald: #29CE41;
$chablis: #FFEFEF;

$theme-colors: (
  primary: $dodger-blue,
  secondary: $emerald,
  success: $mountain-meadow,
  danger: $brick-red,
  warning: $my-sin,
  info: $blue-bayoux,
  dark: $rhino
);

// Headings
$font-size-base: 1rem; // Assumes the browser default, typically `16px`
$font-size-sm: $font-size-base * .1;
$font-size-lg: $font-size-base * 1.25;

$h1-font-size: $font-size-base * 2.25;
$h2-font-size: $font-size-base * 2;
$h3-font-size: $font-size-base * 1.75;
$h4-font-size: $font-size-base * 1.5;
$h5-font-size: $font-size-base * 1.25;
$h6-font-size: $font-size-base;
$headings-font-weight: 500;
$font-weight-bolder: 600;

// Spacing
$pixel-spacer: 5px !default;
// stylelint-disable-next-line scss/dollar-variable-default
@for $i from 1 through 100 {
  $spacers: map-merge($spacers, (
    $i * 5: ($i * $pixel-spacer)
  ));
}

// Buttons
$btn-padding-x: .5rem;
$btn-padding-y: .25rem;
$btn-font-size: 14px;
$btn-line-height: 1.6;
$btn-font-weight: 700;
$btn-border-width: 0;

// Cards
$card-border-radius: 3px;
$card-border-width: 0;

// Forms
$form-group-margin-bottom: 1.25rem;
$input-padding-x: .5rem;
$input-padding-y: 0;
$input-line-height: 1.125rem;
$input-font-size: .875rem;
$input-border-width: 0;
$input-height: 38px;
$input-bg: white;
$input-placeholder-color: $hit-gray;
